/**
 * 1. Use the user's configured `sans` font-family (with Tailwind's default
 *    sans-serif font stack as a fallback) as a sane default.
 */
html {
  font-family: 'Inter', sans-serif;
  font-size: 16px;

  @supports (font-variation-settings: normal) {
    font-family: 'Inter var', sans-serif;
  }

  line-height: 1.5em;
}

/**
 * Use the configured 'mono' font family for elements that
 * are expected to be rendered with a monospace font, falling
 * back to the system monospace stack if there is no configured
 * 'mono' font family.
 */

pre,
code,
kbd,
samp {
  font-family:
    'Fira Code',
    'Menlo',
    'Monaco',
    'Consolas',
    'Liberation Mono',
    'Courier New',
    'monospace';
}

body {
  font-family: 'Nunito', 'arial', 'sans';
  font-size: 18px;
  line-height: 1.6;
}


h1 {
  font-size: 1.7rem;

  .page & {
    font-weight: 700;
  }

}

h2 {
  margin-bottom: 0.5rem;
  background-color: var(--clr-heading-1-bg);
  font-size: 1.5rem
}

h3 {
  background-color: var(--clr-heading-2-bg);
  font-size: 1.3rem;
}

h4 {
  background-color: var(--clr-heading-3-bg);
  font-size: 1.1rem;
}

p {
  padding: 0.5rem;
  font-size: 1.125rem;
  letter-spacing: -0.024%;
}

a {
  color: var(--clr-forward-link);
  text-decoration: none;
  cursor: pointer;

  &:is(:hover, :focus) {
    color: var(--clr-forward-link-transparent);
  }
}

code {
  max-width: 100%;
  overflow: auto;
  border: none;
  border-radius: 4px;
  font-size: 1rem;
  white-space: pre;

  p & {
    display: inline;
  }

}

b, string {
  font-weight: bolder;
}

small {
  font-size: 80%;
}


li {
  letter-spacing: -0.024%;
}

.outline-2 {
  font-size: 1.125rem;
  letter-spacing: -0.025em;
  line-height: 1.5;
}
